.header-warp {
  margin: auto;
  background-image: url(https://img.limeetpet.com/limeet/banner2tobg.png);
  background-repeat: no-repeat;
  background-size: 100%;
  position: relative;
  z-index: 9;
  margin-bottom: 0.46875rem;
}
.header-warp .bg-body-tertiary {
  background: transparent !important;
}
.header-warp .mask {
  height: 12.28125rem;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 11;
  background-color: #D8D8D8;
  opacity: 0.5;
}
.header-warp .arrow-left {
  width: 1.78125rem;
  height: 1.78125rem;
  position: absolute;
  z-index: 15;
  top: 0.625rem;
  left: 0.625rem;
  background-image: url('https://img.limeetpet.com/limeet/limeethouseaorrw-left.png');
  background-position: center;
  background-size: 80%;
  background-repeat: no-repeat;
  cursor: pointer;
}
.header-warp .main {
  position: absolute;
  z-index: 13;
  top: 4.375rem;
  left: 0;
  display: flex;
}
.header-warp .main .des {
  padding-top: 0.46875rem;
  height: 3.125rem;
  color: #000000;
  padding-left: 0.3125rem;
}
.header-warp .main .des a {
  color: #333;
}
.header-warp .main .des h1 {
  font-size: 1.1875rem;
  font-weight: 450;
  height: 1.25rem;
  line-height: 1.25rem;
  text-shadow: 0 0.03125rem 0.0625rem rgba(51, 51, 51, 0.9);
}
.header-warp .main .des h2 {
  font-size: 0.875rem;
  line-height: 0.75rem;
  font-weight: 300;
  text-shadow: 0 0.03125rem 0.0625rem rgba(51, 51, 51, 0.9);
  color: #000;
}
.header-warp .main .des h2 img {
  width: 2.1875rem;
  display: inline-block;
  padding-right: 0.3125rem;
}
.header-warp .main .des p {
  font-size: 0.8125rem;
  line-height: 0.75rem;
  font-weight: 300;
  text-shadow: 0 0.03125rem 0.03125rem rgba(51, 51, 51, 0.9);
  color: #333;
}
.header-warp .main .des img {
  width: 1.125rem;
  display: inline-block;
  padding-right: 0.3125rem;
}
.header-warp .main .logo {
  width: 6.25rem;
  height: 6.25rem;
  padding: 0.15625rem;
  margin-left: 0.625rem;
}
.header-warp .main .logo img {
  width: 6.25rem;
  height: 6.25rem;
}
.header-warp .main:hover {
  opacity: 0.9;
}
.header-warp .header {
  width: 100%;
  height: 10.96875rem;
}
.header-warp .nav-sub {
  width: 100%;
  position: relative;
  z-index: 99;
  font-weight: 500;
}
.header-warp .nav-sub .mask-sub {
  position: absolute;
  width: 100%;
  height: 3.5625rem;
  background-color: #D8D8D8;
  opacity: 0.7;
  left: 0;
  top: 0;
  overflow: hidden;
}
.header-warp .nav-sub ul {
  position: absolute;
  width: 100%;
  height: 1.3125rem;
  z-index: 101;
  padding: 0;
  margin: 0;
  left: 0;
  top: 0;
  display: flex;
}
.header-warp .nav-sub ul li {
  width: 33%;
  height: 1.25rem;
  line-height: 1.25rem;
  text-align: center;
  font-size: 0.5rem;
  text-shadow: 0 0.03125rem 0.03125rem rgba(51, 51, 51, 0.9);
  border-bottom: 0.0625rem solid #D8D8D8;
  cursor: pointer;
}
.header-warp .nav-sub ul li.action {
  color: #000;
  font-weight: 500;
  border-bottom: 0.0625rem solid #F6912D;
}
.header-warp .navbar {
  padding-left: 0.9375rem;
}
.header-warp .navbar a.nav-link {
  padding-right: 0.78125rem;
  padding-left: 0.78125rem;
  cursor: pointer;
  font-weight: bold;
  letter-spacing: 0.0625rem;
}
.header-warp .navbar a:hover {
  color: #F6912D;
}
.header-warp .navbar a.active {
  color: #F6912D;
  font-weight: bold;
}
.header-warp .navbar .dropdown-item.active,
.header-warp .navbar .dropdown-item:active {
  background-color: #D8D8D8;
}
